cava anomalias (nas métricas sobre uso de processador, memória RAM, HD/SSD e processos em execução) de acordo com as regras definindas pelo usuário e enviava notificações nos canais configurados pelo usuário, como: Slack, Discord, Telegram, E-mail e etc. Foram feitas versões desktop com Electron e os clients feitos em GO e bash para servidores Linux. ")])])]),_c('b-col',{staticClass:"mb-5",attrs:{"md":"12","sm":"12","xs":"12"}},[_c('div',{staticClass:"bg-1 p-4 rounded"},[_c('h4',{staticClass:"text-white mt-3 font-weight-bold"},[_vm._v("2020")]),_c('hr',{staticClass:"bg-secondary"}),_c('p',{staticClass:"text-white mt-5"},[_vm._v(" No ano de 2020, junto de uma das equipes da Codions, aperfeiçoamos o sistema Bluee Live, com a criação de novas ferramentas, incluindo aplicações para desktop para transmissão de áudio e vídeo via IP Multicast, que foi vendida para uma empresa de telecomunicações amazonense. "),_c('br'),_vm._v(" Neste mesmo ano, recebi uma grande oportunidade de virar um sócio da empresa e logo aceitei o desafio. Direcionei meus esforços na criação de novos produtos, sendo um deles o Blueead, uma família de soluções focada em educação a distância, que poderiam ser utilizadas em conjunto a partir de integrações. "),_c('br'),_vm._v(" Desenvolvemos o Conttas, um sistema de gestão modular e multitenancy completo, com diversos módulos como: financeiro, CRM, gestão de projetos, contabilidade, orçamentos, portal do cliente e etc. ")])])]),_c('b-col',{staticClass:"mb-5",attrs:{"md":"12","sm":"12","xs":"12"}},[_c('div',{staticClass:"bg-1 p-4 rounded"},[_c('h4',{staticClass:"text-white mt-3 font-weight-bold"},[_vm._v("2019")]),_c('hr',{staticClass:"bg-secondary"}),_c('p',{staticClass:"text-white mt-5"},[_vm._v(" Em 2019 sai da Cartórios Maranhão e fui trabalhar na Codions Tecnologia como Software Engineer. trabalhei em diversas versões do AcademicPro, um AVA (Ambiente Virtual de Aprendizagem) desenvolvido de forma monolítica com Laravel, Mysql e Memcached, sendo utilizado por diversas empresas e instituições públicas como VAT Tecnologia, Ministério Público do Pará, Cena2 Produções e outros. Surgiu uma situação onde precisavamos escalar a plataforma, fizemos diversas refatorações e otimizações em toda a aplicação, tivemos que transformar certos módulos em microserviços com Node.js, adotamos o RabbitMQ para mensageria, substituimos o Memcached pelo Redis e usamos o MongoDB para documentos JSON. "),_c('br'),_vm._v(" Adotamos a Cloudflare para gerenciar configurações de DNS e CDN de assets. Utilizamos os VPS da DigitalOcean para hospedar os projetos e o Docker Swam para fazer a orquestração dos conteiners. O fato dos VPS serem mais limitados, trouxe uma serie de problemas com indisponibilidade dos sistemas, então começamos a estudar algumas soluções da AWS e que no final acabamos optando pelo EKS (o serviço de Kubernetes gerenciado da AWS), que por fim resolveu nossos problemas de indisponibilidade. "),_c('br'),_vm._v(" Junto da minha equipe na Codions, desenvolvemos um conjunto de soluções para webconferências chamada Bluee Live (uma Interface de Gerenciamento para o software BigBlueButton, com algumas ferramentas adicionais), que depois foi licenciado para o SEBRAE-SP e com a Pandemia de covid-19 no início de 2020, passou a ser usado também pela Faculdade Sebrae e diversas outras organizações do setor público e privado. ")])])]),_c('b-col',{staticClass:"mb-5",attrs:{"md":"12","sm":"12","xs":"12"}},[_c('div',{staticClass:"bg-1 p-4 rounded"},[_c('h4',{staticClass:"text-white mt-3 font-weight-bold"},[_vm._v("2018")]),_c('hr',{staticClass:"bg-secondary"}),_c('p',{staticClass:"text-white mt-5"},[_vm._v(" Em 2018 fui contrato pela Associação dos Titulares de Cartório do meu estado (Maranhão), como Full-Stack Developer, onde atuei no desenvolvimento e manutenção de novas funcionalidades, além de refatoração de códigos legados na plataforma Cartórios Maranhão. Propus e desenvolvi um sistema para chamados de atendimento e suporte técnico, o que melhorou muito na velocidade de resolução de problemas tanto dos cartórios quanto dos usuários da plataforma. Fui um dos responsáveis pelo desenvolvimento da plataforma Cartórios Roraima, conseguindo realizar a entrega e implementação em tempo recorde. "),_c('br'),_vm._v(" Fui selecionado pelo presidente da associação para realizar o planejamento, levantamento dos requisitos e desenvolver o \"Indexador\", um sistema de indexação para livros de cartórios, cujo objetivo era a facilitar e modernizar a busca de registros e informações por meio eletrônico. "),_c('br'),_vm._v(" As tecnologias que mais utilizei durante esse período foram: PHP/Laravel, Node.js, Jquery, Vue.js, Mysql, Redis, Docker, aprendi a provisionar servidores Linux (Debian/Ubuntu Server), automatizar processos com Shell Script (como backups de banco de dados e storages), fazer deploy de aplicações com o Deployer dentre diversas outras coisas. ")])])]),_c('b-col',{staticClass:"mb-5",attrs:{"md":"12","sm":"12","xs":"12"}},[_c('div',{staticClass:"bg-1 p-4 rounded"},[_c('h4',{staticClass:"text-white mt-3 font-weight-bold"},[_vm._v("2017")]),_c('hr',{staticClass:"bg-secondary"}),_c('p',{staticClass:"text-white mt-5"},[_vm._v(" Em 2017 tive minha primeira experiência profissional como programador, em uma empresa local, focada em manutenção de computadores e criação de websites. Começei projetando sites com Wordpress (levei quase 2 meses para aprender a criar sites com ele) e rapidamente fui evoluindo. Começei a visitar alguns clientes e fazer a parte de levantamento de requisitos, modelagem de dados e o desenvolvimento de sites e sistemas (geralmente feitos com codeigniter). Durante esse tempo participei de diversos eventos como o Game Jam Plus, onde eu e minha equipe tivemos que desenvolver um jogo completamente do zero em até 48 horas. Estudei Java para Android, criei alguns aplicativos básicos de estudo, incluindo clone de jogos como o Flappy Bird, clone do WhatsApp, Instagram e outros. ")])])]),_c